*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}

.page {
  width: 100%;
  height: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
}

.page__block {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: stretch;
  min-width: 200px;
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: "Arial", "Verdana", sans-serif;
  font-size: 100%;
  line-height: 1.2;
  color: black;
}

.page__block::before {
  content: "";
  display: inline-block;
  background-image: url("../images/background.jpg");
  background-repeat: repeat;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0.5;
  z-index: -1;
}

.header {
  color: #000000;
  border-bottom: 1px solid #2d2d44;
}

.header__title {
  font-size: 32px;
  text-align: center;
}

.main {
  flex: 1 0 auto;
  background: linear-gradient(180deg, rgba(222, 237, 255, 0.6) 0%, rgba(166, 204, 250, 0.5) 100%);
}

.main__article {
  border: 1px solid #2d2d44;
  border-radius: 10px;
  margin: 20px;
  padding: 5px;
}

.polyhedron-block {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: stretch;
}

.polyhedron-block__item {
  border-radius: 10px;
  text-align: center;
  padding: 5px;
  margin: 5px;
  width: 150px;
  background: linear-gradient(180deg, rgba(0, 30, 64, 0.3) 0%, rgba(0, 30, 64, 0.5) 100%);
}

.polyhedron-block__item:hover {
  cursor: pointer;
}

.polyhedron-block__image {
  height: 64px;
  margin: 5px;
}

.polyhedron-block__title {
  margin: 5px;
  color: white;
  font-size: 16px;
}

.footer {
  background: #2d2d44;
  color: #ffffff;
}

.footer__text {
  text-align: center;
}